@mixin content-txt($bg-color, $border) {
    img {
        padding: 60px;
        background: $bg-color;
        opacity: 0.6;
        border-radius: 50%;
        border: $border;
    }

    h5 {
        font-family: 'Spoqa Han Sans Neo';
        font-style: normal;
        font-weight: 500;
        font-size: 0.9rem;
        /* or 20px */
        color: #000000;
    }

    p {
        color: #A1A1A1;
        font-size: 0.8rem;
    }
}

.section08-container {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height:100%;
    padding:100px 0;
    .section08-txt-container {
     
        .section08-sub-txt {
            font-family: 'Spoqa Han Sans Neo';
            font-style: normal;
            font-weight: 700;
            font-size: 1rem;
            color: #949494;
        }

        .section08-head-txt {
            font-family: 'Spoqa Han Sans Neo';
            font-style: normal;
            font-weight: 400;
            font-size: 2.2rem;
            /* or 133% */

            text-align: center;
            letter-spacing: 0.02em;
            color: #444444;
        }
    }

  

    .section08-contents {
        width: 100%;
        height: 400px;
        ul {
            .section08-content-left {
                position: relative;

                li:nth-child(1) {
                    width: 17%;
                    float: left;
                    left: 0px;
                    position: absolute;

                    @include content-txt(#95ABD5, none)
                }

                li:nth-child(2) {
                    width: 17%;
                    float: left;
                    position: absolute;
                    left: 190px;

                    @include content-txt(#C3D8E8, none)
                }

                li:nth-child(3) {
                    width: 17%;
                    left: 380px;
                    float: left;
                    position: absolute;

                    @include content-txt(#FAFAFA, 0.5px solid lightgray)
                }


                li:nth-child(4) {
                    width: 17%;
                    right: 0px;
                    float: left;
                    position: absolute;

                    @include content-txt(#FAFAFA, 0.5px solid lightgray)
                }


                li:nth-child(5) {
                    width: 17%;
                    right: 190px;
                    float: left;
                    position: absolute;

                    @include content-txt(#D9FFC7, none)
                }
            }

        }
    }
}