@import '../../../stylemodules/main.scss';
.section4-ziple-tech {
    padding:100px 0;
    .section4-ziple-tech-headline {
        font-size: 2rem;
        text-align: center;
        color: #444444;

        h2 {
            font-size: 1.8rem;
            color: #5882FA;
        }
    }
    .section4-ziple-tech-container {
        margin-top: 80px;
        padding: 0;
        list-style: none;
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        flex-wrap: wrap;

        .section4-ziple-tech-card {
            .section4-ziple-tech-img {
                width:150px;
                height:150px;
            }
            width: 50%;
            margin-top: 20px;
            display: flex;
            justify-content: center;
            flex-direction: column;
            align-items: center;

            .section4-ziple-tech-title {
                margin: 20px 0;
                font-size: 1.5rem;
                color: #5d626d;
            }

            .section4-ziple-tech-list {
                color: #9da4ad;
                font-size: 0.9rem;
                font-weight: bold;
                display: flex;
                flex-direction: column;

                span {
                    padding-bottom: 5px;
                }
            }
        }
    }
}