.section11-container {
    position: relative;

    .section11-content {
        width: 100%;
        position: absolute;
        top: 150px;
        display: flex;

        .section11-txt {
            width: 50%;

            h4 {
                font-family: 'Poppins';
                font-style: normal;
                font-weight: 600;
                font-size: 1.5rem;
                
                /* identical to box height, or 140% */

                text-align: left;
                letter-spacing: -0.03em;
                
                color: #D0D0D0;
            }

            h1 {
                font-family: 'Spoqa Han Sans Neo';
                font-style: normal;
                font-weight: 500;
                font-size: 2.3rem;
               
                /* or 81% */

                text-align: left;
                letter-spacing: -0.03em;

                color: #263C76;
            }

            p {
                font-family: 'Poppins';
                font-style: normal;
                font-weight: 300;
                font-size: 0.9rem;
          
                word-break: keep-all;
                /* or 140% */

                letter-spacing: -0.03em;
                text-align:left;
                color: #BBBBBB;
            }
        }

        .section11-history-map {

            width: 50%;

            background: red;
            height: 10px;
        }
    }
}